import './App.css';
import { useEffect, useState } from 'react';
import useDidMountEffect from './useDidMountEffect';

function App() {
	const [ total, setTotal ] = useState();
	const [ progress, setProgress ] = useState();
	const [ ratio, setRatio ] = useState();

	const _start = new Date(2021, 8, 27);
	const _now = new Date();
	// const _now = new Date(2022, 2, 26);
	const _end = new Date(2023, 2, 26);

	useEffect(() => {
		console.log('2', _start.toDateString());
		console.log('3', _end.toDateString());
		console.log('4', _now.toDateString());
		setTotal(_end - _start);
		setProgress(_now - _start);
	}, []);

	useDidMountEffect(
		() => {
			setRatio(progress / total);
		},
		[ progress ]
	);

	return (
		<div className="App">
			<div className="view">
				<div className="progress-container">
					<div className="progress" style={{ width: `${ratio * 400}px` }} />
				</div>
				<div className="day-container">
					<span className="day">
						{_now.getFullYear()}년 {_now.getMonth() + 1}월 {_now.getDate()}월
					</span>
				</div>
			</div>
		</div>
	);
}

export default App;
